import { Divider } from 'antd';
import React from 'react';

const radiuses = ['4px', '8px', '16px'];

function Radiuses() {
  return (
    <div className="radiuses">
      <Divider style={{ marginBottom: 60 }}>Radiuses</Divider>
      <div className="container">
        {radiuses.map((radius) => (
          <div key={radius} className="wrapper">
            <div style={{ width: 72, height: 72, borderRadius: radius, background: '#FFACAC' }} />
            <span>{radius}</span>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Radiuses;
