import React from 'react';
import PropTypes from 'prop-types';

function BadgeIcon({ count, multiply }) {
  return (
    <svg
      width={23 * multiply}
      height={18 * multiply}
      viewBox="0 0 23 18"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect width={23} height={18} rx="9" fill="#00953B" />
      <text
        className="button-copy"
        x="50%"
        y="64.7%"
        fill="white"
        dominantBaseline="middle"
        textAnchor="middle"
      >
        {count}
      </text>
    </svg>
  );
}

BadgeIcon.propTypes = {
  multiply: PropTypes.number,
  count: PropTypes.number,
};

BadgeIcon.defaultProps = {
  multiply: 1,
  count: 24,
};

export default BadgeIcon;
