import { Divider } from 'antd';
import React from 'react';

const spacing = [8, 12, 16, 24, 32, 48, 64];

function Spacing() {
  return (
    <div className="spacing">
      <Divider style={{ marginBottom: 60 }}>Spacing Units</Divider>
      <div className="container">
        {spacing.map((item) => (
          <div className="wrapper" key={item}>
            <div style={{ width: item, height: item }} />
            <span>{item} px</span>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Spacing;
