import React from 'react';
import PropTypes from 'prop-types';

const direction = {
  left: 180,
  right: 0,
  top: -90,
  bottom: 90,
};

function ChevronIcon({ color, multiply, type }) {
  return (
    <svg
      style={{ transform: `rotate(${direction[type]}deg)` }}
      width={6 * multiply}
      height={11 * multiply}
      viewBox="0 0 6 11"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M1 9.82666L5.09436 5.41324L1 1.00042"
        stroke={color}
        strokeWidth="1.53636"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}

ChevronIcon.propTypes = {
  color: PropTypes.string,
  multiply: PropTypes.number,
  type: PropTypes.string,
};

ChevronIcon.defaultProps = {
  color: 'white',
  multiply: 1,
  type: 'right',
};

export default ChevronIcon;
