import { Divider } from 'antd';
import React from 'react';

const green = ['green-1', 'green-2', 'green-3', 'green-4', 'green-5', 'green-6', 'white'];
const grey = ['grey-1', 'grey-2', 'grey-3', 'grey-4', 'grey-5', 'black'];

function Colors() {
  return (
    <div className="colors">
      <Divider style={{ marginBottom: 60 }}>Colors</Divider>
      <div className="container">
        {green.map((color) => (
          <div key={color} className="wrapper">
            <div className={color} style={{ width: '75px', height: '68px' }} />
            <span>{color}</span>
          </div>
        ))}
      </div>
      <div className="container">
        {grey.map((color) => (
          <div key={color} className="wrapper">
            <div className={color} style={{ width: '75px', height: '68px' }} />
            <span>{color}</span>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Colors;
