import React from 'react';
import PropTypes from 'prop-types';

function NavigationIcon({ multiply, isActive }) {
  return (
    <svg
      width={20 * multiply}
      height={20 * multiply}
      viewBox="0 0 20 20"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle cx="10" cy="10" r="10" fill={isActive ? '#00953B' : '#B3DFC4'} />
    </svg>
  );
}

NavigationIcon.propTypes = {
  multiply: PropTypes.number,
  isActive: PropTypes.bool,
};

NavigationIcon.defaultProps = {
  multiply: 1,
  isActive: false,
};

export default NavigationIcon;
